@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: 'Roboto', sans-serif;
}

input[type=button] {
    margin: 5px;
    padding: 5px;
    background-color: white;
    border-radius: 5px;
    border-style: solid;
    border-width: 1px;
    border-color: rgb(128, 128, 128);
    /* box-shadow: 0px 0px 5px rgb(128, 128, 128); */
    min-width: 100px;
}

input[type=button]:hover {
    border-color: rgb(142, 178, 194);
    cursor: pointer;
}

input[type=button]:active {
    background-color: rgb(200, 219, 243);
    border-color: rgb(142, 178, 194);
    cursor: pointer;
}

input[type=text] {
    margin: 5px;
    padding: 5px;
    background-color: white;
    border-radius: 5px;
    border-style: solid;
    border-width: 1px;
    border-color: rgb(128, 128, 128);
    /* box-shadow: 0px 0px 5px rgb(128, 128, 128); */
}

input[type=text]:hover {
    border-color: rgb(142, 178, 194);
}

input[type=text]:active {
    border-color: white;
    /* box-shadow: 0px 0px 0px rgb(128, 128, 128); */
}

input[type=text]:focus {
    border-color: rgb(142, 178, 194);
}

.info {
    margin: 5px;
}

/* Copied from https://stackoverflow.com/questions/4151743/how-can-i-change-the-thickness-of-my-hr-tag */
hr {
    border: none;
    height: 1px;
    /* Set the hr color */
    color: gray; /* old IE */
    background-color: gray; /* Modern Browsers */
}

.centered {
    margin: auto;
    text-align: center;
}

